.app-wrapper{
    display: grid;
    width: 1200px;
    margin: 0 auto;

    grid-template-areas:
    'header header'
    'nav content';

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

* {
    margin: 0;
    padding: 0;
}
 img {
     max-width: 100%;
     width: auto;
     height: auto;
 }

 .app-wrapper-content {
     grid-area: content;
     background: cornflowerblue;
 }
